
.fa-social:hover {
  -webkit-transform: scale(1.5);
  -moz-transform: scale(1.5);
  -o-transform: scale(1.5);
}
.fa-social {
  padding: 10px 5%;
  font-size: 2em;
  -webkit-transition-duration: 0.5s;
  -moz-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
}
.footer-wrapper{
  margin: 0;
  padding: 0 5% 1% 5%;
}
.footer-div{
  background: rgba(0, 0, 0, 0.05);
}
.footer-text{
  text-align: justify;
  font-size: 1em;
}
/*
Multicoloured Hover Variations
 */
.social-gh {
  color: #24292e;
}
#social-gh:hover {
  color: #24292e;
}
.social-tw {
  color: #4099FF;
}
#social-tw:hover {
  color: #4099FF;
}
.social-med{
  color: #292929;
}
#social-med:hover {
  color: #292929;
}
.social-tel {
  color: #32a9df;
}
#social-tel:hover {
  color: #32a9df;
}
.social-rdt {
  color: #ff3f18;
}
#social-rdt:hover {
  color: #ff3f18;
}



