
.topics{
  width: 20%;
  float: left;
  padding-right: 10px;
  padding-left: 1.5%;
  position: -webkit-sticky;
  position: sticky;
  top: 5px;
}
.guesses{
  width: 60%;
  min-width: 220px;
  margin-left: 25%;
  position: relative;
}
@media screen and (max-width: 600px) {
.topics {
}
.guesses {
    margin-left:20%;
    width: 77%;
}
}
.pink{
  background-color: pink;
}
.container{
  margin:0;
  padding:0;
}
.voteAlert {
  margin: 5px;
  font-size: 0.7rem;
  border-radius: 2px;
  border-left: 0px !important;
}



